import * as React from "react";
import Footer from "Components/Footer";
import Nav from "Components/Nav";
import SEO from "Components/SEO";
import CenterAlignedText from "Components/CenterAlignedText";
import ATF from "@smallsforsmalls/atf";
import {
	ATF_CONTENT_TYPE,
	FOOTER,
	NAV_CONTENT_TYPE,
	PAGE_CONTENT_TYPE,
	CENTERALIGNEDTEXT_CONTENT_TYPE,
} from "Constants/contentful";
import { PromiseState } from "Constants/promise";
import { Entry } from "contentful";
import ContentfulClientContext from "Contexts/contentful";
import { Page as PageType } from "Types/contentful";

type IProps = {
	path: string;
};

const Page: React.FC<IProps> = ({ path }: IProps) => {
	const client = React.useContext(ContentfulClientContext);

	const [page, setPage] = React.useState<Entry<PageType>>();
	const [loadingState, setLoadingState] = React.useState<PromiseState | void>();

	React.useEffect(() => {
		if (!client) {
			return;
		}

		const getEntriesFn = async (path: string): Promise<void> => {
			setLoadingState(PromiseState.PENDING);
			try {
				const response = await client.getEntries<PageType>({
					content_type: PAGE_CONTENT_TYPE,
					"fields.path": path,
					// We want to resolve references in navigationBar
					include: 4,
				});
				if (response.items.length === 0) {
					throw new Error();
				}
				setPage(response.items[0]);
				setLoadingState(PromiseState.FULFILLED);
				return;
			} catch (e) {
				// We will need a <Redirect> when we enable a router.
				return getEntriesFn("404");
			}
		};

		getEntriesFn(path)
			.then(() => {
				return;
			})
			.catch(() => {
				setLoadingState(PromiseState.REJECTED);
			});
	}, [client, path]);

	if (
		loadingState === PromiseState.REJECTED ||
		(loadingState === PromiseState.FULFILLED && !page)
	) {
		return <p>There was an error</p>;
	}

	if (loadingState === PromiseState.PENDING || !page) {
		return <p>Loading</p>;
	}

	const navModuleIndex = page.fields.components.findIndex(
		(component) => component.sys.contentType.sys.id === NAV_CONTENT_TYPE,
	);

	const atfModule =
		navModuleIndex !== -1 &&
		page.fields.components[navModuleIndex + 1]?.sys.contentType.sys.id ===
			ATF_CONTENT_TYPE
			? page.fields.components[navModuleIndex + 1]
			: null;

	const atfModuleTheme =
		atfModule && "desktopAlignment" in atfModule.fields
			? atfModule.fields.theme
			: null;

	const navTheme = atfModuleTheme != null ? atfModuleTheme === "dark" : null;

	const hasNavAndATF = navModuleIndex > -1 && !!atfModule;

	return (
		<div id="start">
			<SEO
				title={page.fields.title}
				description={page.fields.description}
				keywords={page.fields.keywords}
			/>
			{page.fields.components.map((component) => {
				const contentTypeId = component.sys.contentType.sys.id;
				switch (contentTypeId) {
					case ATF_CONTENT_TYPE: {
						// As you see, we need to filter by property names to make sure
						// that we are dealing with the right component.
						if (!("atfTitle" in component.fields)) {
							return;
						}
						const mainImageFields = component.fields.mainImage?.fields;
						const buttonFields = component.fields.button?.fields;
						const mobileAsset = component.fields.mobileImage;
						const tabletAsset = component.fields.tabletImage;

						const responsiveImagesFields = [];
						if (mobileAsset) {
							responsiveImagesFields.push({
								id: mobileAsset.sys.id,
								width: 720,
								url: mobileAsset.fields.file.url,
							});
						}
						if (tabletAsset) {
							responsiveImagesFields.push({
								id: tabletAsset.sys.id,
								width: 900,
								url: tabletAsset.fields.file.url,
							});
						}

						return (
							<ATF
								key={component.sys.id}
								theme={component.fields.theme}
								mode={component.fields.mode}
								bgColor={component.fields.backgroundColor}
								title={component.fields.atfTitle}
								description={component.fields.subtitle}
								btnLabel={buttonFields?.buttonLabel}
								btnType={buttonFields?.buttonType}
								btnLink={buttonFields?.buttonLink}
								desktopAlignment={component.fields.desktopAlignment}
								mobileAlignment={component.fields.mobileAlignment}
								imageUrl={mainImageFields?.file.url}
								imageTitle={mainImageFields?.title}
								imageDescription={mainImageFields?.description}
								images={responsiveImagesFields}
							/>
						);
					}
					case CENTERALIGNEDTEXT_CONTENT_TYPE: {
						if (!("centerAlignedTextTitle" in component.fields)) {
							return;
						}
						const mainImageFields = component.fields.icon?.fields;
						const buttonFields = component.fields.button?.fields;
						const itemsArray = component.fields.items?.map((el) => {
							const obj = {
								title: el.fields.title,
								image: {
									src: el.fields.mainImage.fields.file.url,
									title: el.fields.mainImage.fields.title,
									description: el.fields.mainImage.fields.description,
								},
							};
							return obj;
						});

						return (
							<CenterAlignedText
								key={component.sys.id}
								theme={component.fields.theme}
								showTitle={component.fields.showTitle}
								moduleBorder={component.fields.moduleBorder}
								bgColor={component.fields.backgroundColor}
								title={component.fields.centerAlignedTextTitle}
								imageUrl={mainImageFields?.file.url}
								imageTitle={mainImageFields?.title}
								imageDescription={mainImageFields?.description}
								btnLabel={buttonFields?.buttonLabel}
								btnType={buttonFields?.buttonType}
								btnLink={buttonFields?.buttonLink}
								useCarousel={component.fields.useCarousel}
								carouselMode={component.fields.carouselMode}
								carouselNavigation={component.fields.carouselNavigation}
								items={itemsArray}
							/>
						);
					}
					case NAV_CONTENT_TYPE: {
						if (!("navTitle" in component.fields)) {
							return;
						}
						const fields = component.fields;
						return (
							<Nav
								key={component.sys.id}
								backgroundColor={fields.backgroundColor}
								getStartedCta={fields.getStartedCta}
								getStartedPath={fields.getStartedPath}
								hasATF={hasNavAndATF}
								leftAlignedLinks={fields.leftAlignedLinks}
								logo={fields.logo}
								logoHref={fields.logoHref}
								rightAlignedLinks={fields.rightAlignedLinks}
								theme={navTheme != null ? navTheme : fields.theme}
							/>
						);
					}
					case FOOTER: {
						const fields =
							"footerTitle" in component.fields ? component.fields : null;
						return fields ? (
							<Footer key={component.sys.id} {...fields} />
						) : null;
					}
					default: {
						return;
					}
				}
			})}
		</div>
	);
};

export default Page;
