import React from "react";
import Wrapper from "@smallsforsmalls/wrapper";
import Grid from "@smallsforsmalls/grid";
import Image from "@smallsforsmalls/image";
import Carousel from "Components/Carousel";
import TextBlock from "@smallsforsmalls/textblock";
import { Button } from "@smallsforsmalls/button";
import styles from "SCSS/centerAlignedText.module.scss";
import clsx from "clsx";

type CenterAlignedTextProps = {
	theme: "light" | "dark";
	bgColor: string;
	title?: string;
	showTitle?: boolean;
	imageUrl?: string;
	imageTitle?: string;
	imageDescription?: string;
	btnLabel?: string;
	btnLink?: string;
	btnType?: "primary" | "secondary";
	moduleBorder?: boolean;
	useCarousel?: boolean;
	carouselNavigation?: "dots" | "none";
	carouselMode?: "full" | "peaking";
	items?: ItemsProps[];
};

type ItemsProps = {
	title?: string;
	image: ImageProps;
};

type ImageProps = {
	images?: ObjectProps[];
	src: string;
	title: string;
	description: string;
};

type ObjectProps = {
	id: string;
	width: number;
	url: string;
};

const CenterAlignedText: React.FC<CenterAlignedTextProps> = ({
	theme,
	bgColor,
	title,
	showTitle = true,
	imageUrl,
	imageTitle = "",
	imageDescription = "",
	btnType = "primary",
	btnLabel,
	btnLink,
	items,
	moduleBorder = false,
	useCarousel = false,
	carouselNavigation = "dots",
	carouselMode = "full",
}: CenterAlignedTextProps) => {
	const classNames = clsx({
		[styles["centeralignedtext-content"]]: true,
		[styles["-off"]]: !useCarousel,
	});

	const slidesClassNames = clsx({
		[styles.slides]: true,
		[styles["-one"]]: items?.length === 1,
		[styles["-two"]]: items?.length === 2,
		[styles["-three"]]: items?.length === 3,
		[styles["-four"]]: items?.length === 4,
		[styles["-five"]]: items?.length === 5,
	});

	const slides = items?.map((el, index) => {
		return (
			<div className={slidesClassNames} key={`${el.title}-${index}`}>
				{el.image.src && (
					<Image
						src={el.image.src}
						description={el.image.description}
						title={el.image.title}
					/>
				)}
				<TextBlock headingLevel="paragraph" title={el.title} />
			</div>
		);
	});

	const showCarousel = items && items.length > 1 && useCarousel;

	return (
		<Wrapper moduleBorder={moduleBorder} theme={theme} bgColor={bgColor}>
			<Grid mode="full">
				<div className={classNames}>
					{imageUrl && (
						<Image
							src={imageUrl}
							title={imageTitle}
							description={imageDescription}
						/>
					)}
					{showTitle && <h3>{title}</h3>}
					{slides && showCarousel ? (
						<Carousel
							until="mobile"
							mode={carouselMode}
							navigation={carouselNavigation}
							cols={20}
						>
							{slides}
						</Carousel>
					) : (
						slides && <div>{slides}</div>
					)}
					{btnLink && (
						<div className={styles.cta}>
							<Button btnType={btnType}>{btnLabel}</Button>
						</div>
					)}
				</div>
			</Grid>
		</Wrapper>
	);
};

export default CenterAlignedText;
